                }"
            ></knob-control>"Stáří"
          </div>
        </div>
      </div>
      <div class="col" id="main">
        <vue-simple-suggest v-model="chosen" :list="getSuggestions" :filter-by-query="false">
          <div slot="suggestion-item" slot-scope="{ suggestion }">
            <div v-html="suggestion"></div>
          </div>
        </vue-simple-suggest>
        <span id="spacer"></span>

        <div class="recommended-group">
          <div class="recommended" v-for="rec in recommended" :key="rec.id">
            <div class="pane">
              <div class="img-wrap">
                <img class="envelope" :src="rec.img" />
              </div>
              <div class="text-wrap">
                <b>
